.header {
  font-size: 40px;
  text-align: center;
}

.page-container {
  padding: 40px;
}

@media screen and (max-width: 1000px) {
  .page-container {
    padding: 30px;
  }
}

@media screen and (max-width: 600px) {
  .page-container {
    padding-top: 20px;
  }
}

@media screen and (max-width: 480px) {
  .page-container {
    margin-top: 48px;
  }
}

@media screen and (max-width: 400px) {
  .page-container {
    padding: 20px;
  }
}



